.animated {
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
}

.animated.infinite {
	-webkit-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
}

.animated.hinge {
	-webkit-animation-duration: 2s;
	animation-duration: 2s;
}

.duration-xs {
	-webkit-animation-duration: 0.25s;
	animation-duration: 0.25s;
}

.duration-sm {
	-webkit-animation-duration: 0.5s;
	animation-duration: 0.5s;
}

.duration-md {
	-webkit-animation-duration: 1s;
	animation-duration: 1s;
}

.duration-lg {
	-webkit-animation-duration: 2s;
	animation-duration: 2s;
}

.duration-xl {
	-webkit-animation-duration: 4s;
	animation-duration: 4s;
}

/*animation-timing-function: sets the animation speed
( linear | ease | ease-in | ease-out | ease-in-out | cubic-bezier ).*/

.timing-linear {
	-webkit-animation-timing-function: linear;
	animation-timing-function: linear;
}

.timing-ease-in {
	-webkit-animation-timing-function: ease-in;
	animation-timing-function: ease-in;
}

.timing-ease-out {
	-webkit-animation-timing-function: ease-out;
	animation-timing-function: ease-out;
}

.timing-ease-in-out {
	-webkit-animation-timing-function: ease-in-out;
	animation-timing-function: ease-in-out;
}

.timing-cubic-bezier {
	-webkit-animation-timing-function: cubic-bezier;
	animation-timing-function: cubic-bezier;
}

/*animation-delay: the delay before our animation will start.*/

.delay-xs {
	-webkit-animation-delay: 0.25s;
	animation-delay: 0.25s;
}

.delay-sm {
	-webkit-animation-delay: 0.5s;
	animation-delay: 0.5s;
}

.delay-md {
	-webkit-animation-delay: 1s;
	animation-delay: 1s;
}

.delay-lg {
	-webkit-animation-delay: 2s;
	animation-delay: 2s;
}

.delay-xl {
	-webkit-animation-delay: 4s;
	animation-delay: 4s;
}

/*how many times it will iterate through animation.*/

.iteration-infinite {
	-webkit-animation-iteration-count: infinite;
	animation-iteration-count: infinite;
}

.iteration-1 {
	-webkit-animation-iteration-count: 1;
	animation-iteration-count: 1;
}

.iteration-2 {
	-webkit-animation-iteration-count: 2;
	animation-iteration-count: 2;
}

.iteration-3 {
	-webkit-animation-iteration-count: 3;
	animation-iteration-count: 3;
}

.iteration-4 {
	-webkit-animation-iteration-count: 2;
	animation-iteration-count: 2;
}

.iteration-5 {
	-webkit-animation-iteration-count: 4;
	animation-iteration-count: 4;
}

/* animation-direction: gives you the ability to change the loop direction, from start to end ,
or from end to start, or both.*/

.direction-normal {
	-webkit-animation-direction: normal;
	animation-direction: normal;
}

.direction-reverse {
	-webkit-animation-direction: reverse;
	animation-direction: reverse;
}

.direction-alternate {
	-webkit-animation-direction: alternate;
	animation-direction: alternate;
}

.direction-alternate-reverse {
	-webkit-animation-direction: alternate-reverse;
	animation-direction: alternate-reverse;
}

/* animation-fill-mode: specifies which styles will be applied to the element when our animation is finished*/

.fill-mode-none {
	-webkit-animation-fill-mode: none;
	animation-fill-mode: none;
}

.fill-mode-forwards {
	-webkit-animation-fill-mode: forwards;
	animation-fill-mode: forwards;
}

.fill-mode-backwards {
	-webkit-animation-fill-mode: backwards;
	animation-fill-mode: backwards;
}

.fill-mode-both {
	-webkit-animation-fill-mode: both;
	animation-fill-mode: both;
}

/*
 *@name pulse
 *@className pulse animated
 *@cssCode true
 *@editionLink codepen.io
 *@author Nick Pettit
 *@source Glide
 *@sourceUrl https://github.com/nickpettit/glide
 *@issues https://github.com/daneden/animate.css/issues
 *@license MIT
 */

@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale3d(1, 1, 1);
		transform: scale3d(1, 1, 1);
	}

	50% {
		-webkit-transform: scale3d(1.05, 1.05, 1.05);
		transform: scale3d(1.05, 1.05, 1.05);
	}

	100% {
		-webkit-transform: scale3d(1, 1, 1);
		transform: scale3d(1, 1, 1);
	}
}

@keyframes pulse {
	0% {
		-webkit-transform: scale3d(1, 1, 1);
		-ms-transform: scale3d(1, 1, 1);
		transform: scale3d(1, 1, 1);
	}

	50% {
		-webkit-transform: scale3d(1.05, 1.05, 1.05);
		-ms-transform: scale3d(1.05, 1.05, 1.05);
		transform: scale3d(1.05, 1.05, 1.05);
	}

	100% {
		-webkit-transform: scale3d(1, 1, 1);
		-ms-transform: scale3d(1, 1, 1);
		transform: scale3d(1, 1, 1);
	}
}

.pulse {
	-webkit-animation-name: pulse;
	animation-name: pulse;
}

/*
 *@name rubberBand
